.Lottery {
  background-color: #e081142f;
  height: 35vh;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  border: 2px solid rgba(0, 0, 0, 0.247);
  margin-bottom: 10px;
}
.loteryWindow {
  display: flex;
  justify-content: center;
}
.Lottery button {
  margin-top: 15px;
  padding: 20px 50px;
  border-radius: 25px;
  border: none;
  transition: all 0.5s ease-in;
  background-color: rgba(37, 37, 116, 0.568);
  color: white;
  font-weight: 700;
}
.Lottery button:hover {
  outline: none;
  padding: 30px 70px;
}
.Lottery button:focus {
  outline: none;
  /* padding: 30px 70px; */
  background-color: rgb(194, 43, 17);
}
